.movie img {
  position: relative;
  top: -50px;
  max-width: 180px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie-title,
.movie .movie-year {
  margin: 0;
  font-weight: 300;
}

.movie .movie-title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie-genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}

.movie-genres li,
.movie .movie-year {
  margin-right: 10px;
  font-size: 14px;
}


@media screen and (max-width:1280px) {
  .movies a {
    flex: 100%;
  }
  .movies .movie {
    width: 100%;

  }
}